.dropdown-menu {
    background: red;
    width: 200px;
    position: absolute;
    top: 70px;
    list-style: none;
    text-align: start;
    border-radius: 10px;
    box-shadow: 4px 4px 20px 1px rgba(23, 19, 92, 0.2);
    z-index: 200;
}

.dropdown-menu li {
    background: #0e0430;
    cursor: pointer;
    z-index: 200;
}

.dropdown-menu li:hover {
    background: #e6731c;
}

.dropdown-menu.clicked {
    display: none;
}

.dropdown-link {
    display: block;
    height: 100%;
    width: 100%;
    text-decoration: none;
    color: #fff;
    padding: 16px;
}

@media screen and (max-width: 960px) {
    .fa-caret-down {
        display: none;
    }
}